import React, { useEffect, useRef, useState } from 'react'
import { GatsbyImage, IGatsbyImageData } from 'gatsby-plugin-image'
import { useBreakpoint } from 'gatsby-plugin-breakpoints'
import { faAngleLeft, faAngleRight } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import ImageDetail from '@/containers/project/ImageDetail'

export default function ImageListContainer({
  imageList400,
  imageList200,
  srcList,
}: {
  imageList400: IGatsbyImageData[]
  imageList200: IGatsbyImageData[]
  srcList: string[]
}) {
  const breakpoints = useBreakpoint()

  const [isScrolledToLeft, setIsScrolledToLeft] = useState(true)
  const [isScrolledToRight, setIsScrolledToRight] = useState(false)
  const [isOpen, setIsOpen] = useState(false)
  const [number, setNumber] = useState(0)

  const scrollRef = useRef<HTMLDivElement>(null) // ìŠ¤í¬ë¡¤ ê°€ëŠ¥í•œ divì— ëŒ€í•œ ì°¸ì¡°ë¥¼ ìƒì„±í•©ë‹ˆë‹¤.
  // ì™¼ìª½ìœ¼ë¡œ ìŠ¤í¬ë¡¤í•˜ëŠ” í•¨ìˆ˜
  const scrollToLeft = () => {
    if (scrollRef.current) {
      scrollRef.current.scrollBy({ left: breakpoints.lg ? -350 : -800, behavior: 'smooth' }) // ì—¬ê¸°ì„œ -300ì€ ìŠ¤í¬ë¡¤í•  ì–‘ì…ë‹ˆë‹¤. í•„ìš”ì— ë”°ë¼ ì¡°ì ˆí•˜ì„¸ìš”.
    }
  }

  // ì˜¤ë¥¸ìª½ìœ¼ë¡œ ìŠ¤í¬ë¡¤í•˜ëŠ” í•¨ìˆ˜
  const scrollToRight = () => {
    if (scrollRef.current) {
      scrollRef.current.scrollBy({ left: breakpoints.lg ? 350 : 800, behavior: 'smooth' }) // ì—¬ê¸°ì„œ 300ì€ ìŠ¤í¬ë¡¤í•  ì–‘ì…ë‹ˆë‹¤. í•„ìš”ì— ë”°ë¼ ì¡°ì ˆí•˜ì„¸ìš”.
    }
  }

  const checkScroll = () => {
    if (scrollRef.current) {
      const { scrollLeft, scrollWidth, clientWidth } = scrollRef.current
      const isRight = scrollLeft + clientWidth >= scrollWidth
      const isLeft = scrollLeft <= 0 // ì™¼ìª½ ëì— ë„ë‹¬í–ˆëŠ”ì§€ í™•ì¸
      setIsScrolledToRight(isRight)
      setIsScrolledToLeft(isLeft)
    }
  }

  useEffect(() => {
    const scrollContainer = scrollRef.current
    if (scrollContainer) {
      scrollContainer.addEventListener('scroll', checkScroll)
      checkScroll() // ì»´í¬ë„ŒíŠ¸ ë§ˆìš´íŠ¸ ì‹œ ì´ˆê¸° ìŠ¤í¬ë¡¤ ìœ„ì¹˜ í™•ì¸
    }

    return () => {
      if (scrollContainer) {
        scrollContainer.removeEventListener('scroll', checkScroll)
      }
    }
  }, [])

  return imageList400.length > 0 ? (
    <>
      <div className="flex justify-center my-10 bg-neutral-100 relative">
        {!isScrolledToLeft && (
          <div className="absolute z-10 -left-7 max-lg:-left-4 top-0 bottom-0 flex items-center">
            <button
              type="button"
              aria-label="ì™¼ìª½ ë”ë³´ê¸°"
              className="bg-white rounded-full shadow-[0_1px_2px_0_rgba(0,0,0,0.3)] h-14 w-14 max-lg:h-10 max-lg:w-10"
              onClick={scrollToLeft}
            >
              <FontAwesomeIcon size="lg" icon={faAngleLeft} />
            </button>
          </div>
        )}

        <div className="flex gap-5 overflow-x-scroll hide-scroll-bar" ref={scrollRef}>
          {breakpoints.lg
            ? imageList200.map((image: IGatsbyImageData, index) => (
                <button
                  type="button"
                  onClick={() => {
                    setNumber(index)
                    setIsOpen(true)
                  }}
                  aria-label={`${index} ë²ˆì§¸ ì´ë¯¸ì§€`}
                  key={image.images.fallback?.src || `image-${index}`}
                >
                  <GatsbyImage
                    alt={`${index} ë²ˆì§¸ ì´ë¯¸ì§€`}
                    image={image}
                    className="rounded h-full min-w-max hover:cursor-pointer"
                    objectFit="contain"
                    onClick={() => {
                      setNumber(index)
                      setIsOpen(true)
                    }}
                  />
                </button>
              ))
            : imageList400.map((image: IGatsbyImageData, index) => (
                <button
                  type="button"
                  onClick={() => {
                    setNumber(index)
                    setIsOpen(true)
                  }}
                  aria-label={`${index} ë²ˆì§¸ ì´ë¯¸ì§€`}
                  key={image.images.fallback?.src || `image-${index}`}
                >
                  <GatsbyImage
                    alt=""
                    image={image}
                    className="rounded h-full min-w-max hover:cursor-pointer"
                    objectFit="contain"
                    key={image.images.fallback?.src || `image-${index}`}
                    onClick={() => {
                      setNumber(index)
                      setIsOpen(true)
                    }}
                  />
                </button>
              ))}
        </div>

        {!isScrolledToRight && (
          <div className="absolute z-10 -right-7 max-lg:-right-4 top-0 bottom-0 flex items-center">
            <button
              type="button"
              aria-label="ì˜¤ë¥¸ìª½ ë”ë³´ê¸°"
              className="bg-white rounded-full shadow-[0_1px_2px_0_rgba(0,0,0,0.3)] h-14 w-14 max-lg:h-10 max-lg:w-10"
              onClick={scrollToRight}
            >
              <FontAwesomeIcon size="lg" icon={faAngleRight} />
            </button>
          </div>
        )}
      </div>
      {isOpen ? (
        <ImageDetail
          srcList={srcList}
          number={number}
          close={() => setIsOpen(false)}
          toLeft={() => setNumber(number - 1 < 0 ? 0 : number - 1)}
          toRight={() => setNumber(number + 1 > srcList.length - 1 ? srcList.length - 1 : number + 1)}
        />
      ) : null}
    </>
  ) : null
}
